import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/sales/services/language.service';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { NzModalService } from 'ng-zorro-antd/modal';

@Component({
  selector: 'app-employee-group',
  templateUrl: './employee-group.component.html',
  styleUrls: ['./employee-group.component.scss']
})
export class EmployeeGroupComponent implements OnInit {

  newNameGroup: any;
  employee_group: any = [];
  commission: any = []
  id_commission = ''
  constructor(
    private router: Router,
    private vhQuerySales: VhQuerySales,
    private vhAlgorithm: VhAlgorithm,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    private vhQuery: VhQuery,
    private vhAuth: VhAuth,
    private nzModalService: NzModalService
  ) {
  }

  ngOnInit(): void {
    this.getGroup();
  }
  /**
   * get dữ liệu hạng nv và công thức hoa hổng
   * @example  this.getGroup();
   */
  getGroup() {
    Promise.all([
      this.vhQuerySales.getCommissions(),
      this.vhQuerySales.getEmployeeClasss()
    ])
      .then(([commission, response]) => {
        

        this.commission = commission
        this.employee_group = this.vhAlgorithm.sortStringbyASC(response, 'name').map(item => {
          return { ...item,id_commission : item.id_commission ? item.id_commission : '' ,number_customer: this.vhAuth.getlocalEmployees().filter(i => i.id_employee_class == item._id).length }
        })

      })

  }

  /**Kiểm tra tồn tại tên hạng nv */
  checkExistGroup(groupName: string): Promise<boolean> {
    return new Promise<any>((resolve, reject) => {
      let index = this.employee_group.findIndex(item =>
        this.vhAlgorithm.changeAlias(item.name).toLowerCase() == this.vhAlgorithm.changeAlias(groupName).toLowerCase())
      if (index == -1) resolve(false)
      else resolve(true);
    });
  }


  /*----Add group product----*/
  addGroupForm: FormGroup;
  visible_add = false; //biến cờ trạng thái show/hide popup thêm hạng nv

  /**
   * mở modal thêm hạng nv
   * @example this.openAdd()
   */
  openAdd(): void {
    this.visible_add = true;
    this.id_commission = ''
  }
  
  /**
   * đóng modal thêm hạng nv
   * @example this.closeAdd()
   */
  closeAdd(): void {
    this.visible_add = false;
    this.id_commission = ''
  }
  getValue(value) {
    this.newNameGroup = value;
  }
  /**
   * thêm hạng nv vào DB
   * @example this.addGroup()
   */
  addGroup() {
    if (this.newNameGroup) {
      this.checkExistGroup(this.newNameGroup)
        .then((res) => {
          if (res) {
            this.vhComponent.showToast(1500, this.languageService.translate("Group existed"), 'alert-toast');
            return;
          } else {
            this.vhQuerySales.addEmployeeClass({ name: this.newNameGroup, id_commission: this.id_commission ? this.id_commission : null }).then((response: any) => {
              this.employee_group.push({ ...response, number_customer: 0 });
              this.vhComponent.alertMessageDesktop('success', this.languageService.translate("Thêm nhóm nhân viên thành công"))
              this.closeAdd();
            }, error => {
              
            })
          }
        }, err => { })
    }
    else this.vhComponent.alertMessageDesktop('error', this.languageService.translate("Vui lòng nhập tên nhóm"))
  }

  /** ----Edit group product----- */
  visible_detail = false;
  name_group: any;
  _id: any;
  /**
   * mở modal cập nhật hạng nv
   * @param item 
   * @example this.openEdit(employee_class_selected)
   */
  openEdit(item): void {
    this.visible_detail = true;
    this.newNameGroup = item.name;
    this._id = item._id;
    this.id_commission = item.id_commission
  }
  /**
   * đóng modal cập nhật hạng nv
   * @example this.closeEdit();
   */
  closeEdit(): void {
    this.visible_detail = false;
    this.id_commission = ''
  }
  /**
   * cập nhật hạng nv
   *  @example this.editGroup();
   */
  editGroup() {
    this.vhQuerySales.updateEmployeeClass(this._id, { name: this.newNameGroup, id_commission: this.id_commission ? this.id_commission :null })
      .then((bool) => {
        if (bool) {
          this.employee_group[this.employee_group.findIndex(item => item._id == this._id)].name = this.newNameGroup;
          this.employee_group[this.employee_group.findIndex(item => item._id == this._id)].id_commission = this.id_commission;
          

        }
        this.closeEdit();
      }, error => {
        
      })

  }
  /** ------------------------- */
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/partner']);
  }

  /**
   * xóa hạng nv khỏi DB nếu đủ điều kiện
   * @param data 
   */
  deleteGroup(data) {
    if (data.number_customer) {
      this.nzModalService.confirm({
        nzTitle: this.languageService.translate("Nhóm này đang có nhân viên! Đồng ý xóa?"),
        nzOnOk: () => {
          this.vhComponent.showLoading('').then(() => {
            this.vhQuerySales.deleteEmployeeClass(data._id).then(bool => {
              if (bool) {
                let customers = this.vhAuth.getlocalEmployees().filter(i => i.id_employee_class == data._id);
                let promise = [];
                for (let i in customers) {
                  promise[i] = this.vhAuth.updateEmployee(customers[i]._id, { id_employee_class: null })
                }
                this.vhQuery.asyncPromiseAll(promise).then((array) => {
                  this.employee_group.splice(this.employee_group.indexOf(data), 1)
                  this.vhComponent.alertMessageDesktop('success', this.languageService.translate("Xóa nhóm nhân viên thành công"))
                  this.vhComponent.hideLoading(0)
                }, error => {  })
              }
            })
          })

        },
        nzCancelText: this.languageService.translate("Cancel")
      })
    }
    else {
      this.vhComponent.showLoading('').then(() => {
        this.vhQuerySales.deleteEmployeeClass(  data._id).then(bool => {
          if (bool) {
            this.employee_group.splice(this.employee_group.indexOf(data), 1)
            this.vhComponent.alertMessageDesktop('success', this.languageService.translate("Xóa nhóm nhân viên thành công"))
          }
        }).finally(() => this.vhComponent.hideLoading(0))
      })

    }

  }

  gotoDetailGroup(data) {
    this.router.navigate(['/sales/dashboard/manage/partner/employee-group/detail'], { state: { group: data } });
  }

  gotoAddCommission(){
    this.router.navigate(['/sales/dashboard/manage/sales-program/discount-employee/sales-discount/list-discount'])
  }
}
