import { ScrollingModule } from '@angular/cdk/scrolling';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { EmployeeGroupComponent } from './employee-group.component';
import { PermissionComponent } from './permission/permission.component';
import { DetailGroupComponent } from './detail-group/detail-group.component';
import { EditGroupComponent } from './edit-group/edit-group.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';

const routes: Routes = [
  {
    path: '',
    component: EmployeeGroupComponent
  },
  {
    path: 'detail',
    component: DetailGroupComponent
  },
  {
    path: 'edit',
    component: EditGroupComponent
  }
]

@NgModule({
  declarations: [
    EmployeeGroupComponent,
    PermissionComponent,
    DetailGroupComponent,
    EditGroupComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    ScrollingModule,
    NgZorroAntModule,
    FormsModule,
    ReactiveFormsModule,
    TranslateModule,
    IonicModule
  ]
})
export class EmployeeGroupModule { }
