
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
@Component({
  selector: 'app-detail-group',
  templateUrl: './detail-group.component.html',
  styleUrls: ['./detail-group.component.scss']
})
export class DetailGroupComponent  {
  list_employee_valid= [];
  list_search_employee_valid= [];
  list_employee: any = [];
  list_search_employee= [];
  isVisibleModalAdd: boolean = false;
  group: any;
  employeeGroups: any[] = [];
  constructor(
    private router: Router,
    private vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
    private lang: LanguageService,
    private vhAuth:VhAuth,
    private cdRef: ChangeDetectorRef,
    private vhComponent:VhComponent,
    private vhQuery:VhQuery
  ) {
    this.group = this.router.getCurrentNavigation().extras.state.group
  }
  ngAfterViewInit() {
    this.doRefreshData()
    this.vhQuerySales.getEmployeeClasss().then((response) => {
      this.employeeGroups = this.vhAlgorithm.sortStringbyASC(response, 'name')
    })
  }
  /**get data product */
  doRefreshData() {
    this.list_employee = this.vhAuth.getlocalEmployees().filter(item => item.id_employee_class == this.group._id)
    this.list_search_employee = this.list_employee
  }
  heightScroll: string;
  ngAfterViewChecked() {
    if (document.querySelector("#detail-group-employees") && document.querySelector(".ant-table-thead") && document.querySelector(".detail-group-employees-header")  ) {
      this.heightScroll = document.querySelector("#detail-group-employees").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".detail-group-employees-header").clientHeight  - 110 + "px";
    }
    this.cdRef.detectChanges();
  }
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/partner/employee-group']);
  }

  groupChange(ev, data) {
    this.vhAuth.updateEmployee(data._id, {id_employee_class: ev})
    .catch((err)=>{console.error(err)})
  }
 /*---tìm kiếm khách hàng theo tên và email---*/
 searchCustomer(event) {
  let value = this.vhAlgorithm.changeAlias(event.toLowerCase());
  this.list_employee = this.vhAlgorithm.searchList(
    value,
    this.list_search_employee,
    ["name", "phone", "email"]
  );
}
  /*---tìm kiếm khách hàng theo tên và email---*/
  searchCustomerModal(event) {
    let value = this.vhAlgorithm.changeAlias(event.toLowerCase());
    this.list_employee_valid = this.vhAlgorithm.searchList(
      value,
      this.list_search_employee_valid,
      ["name", "phone", "email"]
    );
  }
  // gotoDetailCustomer(data){
  //   this.router.navigate(['/sales/dashboard/manage/partner/employee-group/edit'],{state:{data:data, group : this.group}});
  // }
  handleCancel() {
    this.isVisibleModalAdd = false;

  }

  handleOk() {
    this.vhComponent.showLoading('')
    .then(() =>{
      let promise = new Array();
      let arr = this.list_employee_valid.filter(item => item.checked)
      for (let i = 0; i < arr.length; i++) {
        promise[i] = this.vhAuth.updateEmployee(arr[i]._id, {id_employee_class : this.group._id})
      }
      this.vhQuery.asyncPromiseAll(promise)
        .then((array) => {
          this.vhComponent.hideLoading(0)
          // this.list_employee = this.list_employee.concat(arr.map(item => {return {...item,id_employee_class : this.group._id }}))
          this.isVisibleModalAdd = false;
          this.doRefreshData()

        }, error => {  })
    })

  }

  openModal() {
    this.isVisibleModalAdd = true;
    this.list_employee_valid = this.vhAuth.getlocalEmployees().filter(item => !item.id_employee_class).map(ele => {return {...ele, checked: false}})
    this.list_search_employee_valid = this.list_employee_valid
  }

  /** hàm sort cho các cột */
  nameCol = false;
  phoneCol = false;
  emailCol = false;
  addressCol = false;
  /** Hàm thực hiện sắp xếp theo collection
   *
   * @param colName       // tên cột muôn sắp xếp
   */
  sortTable(colName){
    switch (colName) {
      case 'name':
        if(this.nameCol){
          this.list_employee = this.vhAlgorithm.sortVietnamesebyASC([...this.list_employee], colName);
        }else{
          this.list_employee = this.vhAlgorithm.sortVietnamesebyDESC([...this.list_employee], colName);
        }
        break;
      case 'phone':
        if(this.phoneCol){
          this.list_employee = this.vhAlgorithm.sortNumberbyASC([...this.list_employee], colName);
        }else{
          this.list_employee = this.vhAlgorithm.sortNumberbyDESC([...this.list_employee], colName);
        }
        break;
      case 'email':
        if(this.emailCol){
          this.list_employee = this.vhAlgorithm.sortStringbyASC([...this.list_employee], colName);
        }else{
          this.list_employee = this.vhAlgorithm.sortStringbyDESC([...this.list_employee], colName);
        }
        break;
      case 'address':
        if(this.addressCol){
          this.list_employee = this.vhAlgorithm.sortVietnamesebyASC([...this.list_employee], colName);
        }else{
          this.list_employee = this.vhAlgorithm.sortVietnamesebyDESC([...this.list_employee], colName);
        }
        break;
    }
  }
}
